
























import Vue from 'vue';
import {supportPositions} from '../filters/OverlayFilter';

export default Vue.extend({
  props: ['settings'],
  data() {
    return {
      supportPositions: Object.entries(supportPositions)
        .filter(([position]) => isNaN(Number(position)))
        .map(([position, index]) => {
          return {text: position, value: index};
        }),
    };
  },
});
